import React from 'react';
import { Link } from 'react-router-dom';

// material-ui
import { makeStyles } from '@material-ui/core/styles';
import {
	Box,
	Button,
	FormControl,
	FormHelperText,
	IconButton,
	InputAdornment,
	InputLabel,
	OutlinedInput,
	Typography,
} from '@material-ui/core';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// project imports
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useDispatch } from 'react-redux';
import { useAuth } from '../../../../hooks';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import { SNACKBAR_OPEN } from '../../../../store/actions';
import { validationErrors } from '../constants';

// assets

// style constant
const useStyles = makeStyles(theme => ({
	loginInput: {
		...theme.typography.customInput,
	},
}));

const AWSLogin = (props: { login?: number }, { ...others }) => {
	const classes = useStyles();
	const { login } = useAuth();
	const dispatch = useDispatch();

	const [showPassword, setShowPassword] = React.useState(false);
	const handleClickShowPassword = () => {
		setShowPassword(!showPassword);
	};

	const handleMouseDownPassword = (event: React.SyntheticEvent) => {
		event.preventDefault();
	};

	return (
		<>
			<Formik
				initialValues={{
					username: '',
					password: '',
					submit: null,
				}}
				validationSchema={Yup.object().shape({
					username: Yup.string().max(255).required(validationErrors.requiredUsername),
					password: Yup.string().max(255).required(validationErrors.requiredPassword),
				})}
				onSubmit={async (values, { setStatus, setSubmitting }) => {
					try {
						await login(values);
					} catch (err: any) {
						setStatus({ success: false });
						setSubmitting(false);
						dispatch({
							type: SNACKBAR_OPEN,
							open: true,
							message: err.message,
							variant: 'alert',
							alertSeverity: 'error',
							anchorOrigin: { vertical: 'top', horizontal: 'center' },
						});
					}
				}}
			>
				{({
					errors,
					handleBlur,
					handleChange,
					handleSubmit,
					isSubmitting,
					touched,
					values,
				}) => (
					<form noValidate onSubmit={handleSubmit} {...others}>
						<FormControl
							fullWidth
							error={Boolean(touched.username && errors.username)}
							className={classes.loginInput}
							variant='outlined'
						>
							<InputLabel htmlFor='outlined-adornment-username-login'>
								Username
							</InputLabel>
							<OutlinedInput
								id='outlined-adornment-username-login'
								type='text'
								value={values.username}
								name='username'
								onBlur={handleBlur}
								onChange={handleChange}
								label='Username'
								inputProps={{}}
							/>
							{touched.username && errors.username && (
								<FormHelperText error id='standard-weight-helper-text-username-login'>
									{' '}
									{errors.username}{' '}
								</FormHelperText>
							)}
						</FormControl>

						<FormControl
							fullWidth
							error={Boolean(touched.password && errors.password)}
							className={classes.loginInput}
							variant='outlined'
						>
							<InputLabel htmlFor='outlined-adornment-password-login'>
								Password
							</InputLabel>
							<OutlinedInput
								id='outlined-adornment-password-login'
								type={showPassword ? 'text' : 'password'}
								value={values.password}
								name='password'
								onBlur={handleBlur}
								onChange={handleChange}
								endAdornment={
									<InputAdornment position='end'>
										<IconButton
											aria-label='toggle password visibility'
											onClick={handleClickShowPassword}
											onMouseDown={handleMouseDownPassword}
											edge='end'
										>
											{showPassword ? <Visibility /> : <VisibilityOff />}
										</IconButton>
									</InputAdornment>
								}
								label='Password'
								inputProps={{}}
							/>
							{touched.password && errors.password && (
								<FormHelperText error id='standard-weight-helper-text-password-login'>
									{' '}
									{errors.password}{' '}
								</FormHelperText>
							)}
						</FormControl>
						{errors.submit && (
							<Box mt={3}>
								<FormHelperText error>{errors.submit}</FormHelperText>
							</Box>
						)}

						<Box mt={2}>
							<AnimateButton>
								<Button
									disableElevation
									disabled={isSubmitting}
									fullWidth
									size='large'
									type='submit'
									variant='contained'
									color='secondary'
								>
									Sign in
								</Button>
							</AnimateButton>
						</Box>
						<Typography
							variant="subtitle1"
							component={Link}
							to="forgotPassword"
						>
							Forgot password
						</Typography>
					</form>
				)}
			</Formik>
		</>
	);
};

export default AWSLogin;
